import React from "react";
import g from "glamorous";
import { withPrefix } from "gatsby-link"

import Navbar from "../components/navbar";
// import { rhythm } from "../utils/typography";

export default ({ data }) => {
  console.log('page', data);
  return (
    <div className="homepage">
      <div className="jumbotron">
        <div className="container">
          <h1 className="h1">{data.site.siteMetadata.homepage.jumbotron.caption}</h1>
          <p className="p1">{data.site.siteMetadata.homepage.jumbotron.des}</p>
        </div>

        <Navbar data={data} active="" />
      </div>
      <div className="section section-investments">
        <div className="container">
          <h2 className="h2 title">{data.site.siteMetadata.homepage.section1.title}</h2>
          <p className="p1 des">{data.site.siteMetadata.homepage.section1.des}</p>
          <button className="btn">
            <a className="link" href={data.site.siteMetadata.navbar.href1}>
              {data.site.siteMetadata.homepage.section1.btn}
            </a>
          </button>
        </div>
      </div>
      <div className="section section-approach">
        <div className="container">
          <h2 className="h2 title">{data.site.siteMetadata.homepage.section2.title}</h2>
          <p className="p1 des">{data.site.siteMetadata.homepage.section2.des}</p>
          <button className="btn">
            <a className="link" href={data.site.siteMetadata.navbar.href2}>
              {data.site.siteMetadata.homepage.section2.btn}
            </a>
          </button>
        </div>
      </div>
      <div className="section section-team">
        <div className="container">
          <h2 className="h2 title">{data.site.siteMetadata.homepage.section3.title}</h2>
          <p className="p1 des">{data.site.siteMetadata.homepage.section3.des}</p>
          <div className="member-group">

            {data.site.siteMetadata.homepage.members.map((member, i) => {
              return (
                <div className="member" key={i}>
                  <img className="image" src={`/static/${member.imgThumbnail}`} />
                  <div className="info">
                    <span className="name">{member.name}</span>
                    <span className="p1 title">{member.title}</span>
                    <span className="p1 expert">{member.expert}</span>
                  </div>
                </div>
              );
            })}

          </div>
          <button className="btn">
            <a className="link" href={data.site.siteMetadata.navbar.href3}>
              {data.site.siteMetadata.homepage.section3.btn}
            </a>
          </button>
        </div>
      </div>
    </div>
  );
};

export const query = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        navbar {
          navs
          nav1
          nav2
          nav3
          nav4
          href1
          href2
          href3
          href4
        }
        homepage {
          jumbotron {
            caption
            des
          }
          section1 {
            title
            des
            btn
          }
          section2 {
            title
            des
            btn
          }
          section3 {
            title
            des
            btn
          }
          members {
            name
            title
            imgThumbnail
          }
        }
      }
    }
  }
 `


